html {
  font-family: "Noto Serif JP", serif;
  font-size: 0.5208333333vw;
}

body {
  overflow-x: hidden;
  font-family: "Noto Serif JP", serif;
}

body.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 0;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

picture {
  display: block;
}

a[href^="tel:"] {
  pointer-events: none;
}

@keyframes scroll-lead {
  0% {
    transform: scale(0, 1);
    transform-origin: 0 0;
  }

  50% {
    transform: scale(1, 1);
    transform-origin: 0 0;
  }

  51% {
    transform: scale(1, 1);
    transform-origin: 100% 0;
  }

  100% {
    transform: scale(0, 1);
    transform-origin: 100% 0;
  }
}

@keyframes bgextendAnimeBase {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes bgextendAnimeSecond {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeUpAnime {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes rotateAnime {
  from {
    transform: rotateZ(0deg);
  }

  to {
    transform: rotateZ(360deg);
  }
}

@keyframes zoomUpAnime {
  0% {
    opacity: 1;
    transform: rotateZ(0deg) scale(1);
  }

  30% {
    opacity: 1;
    transform: rotateZ(360deg) scale(1);
  }

  to {
    opacity: 0;
    transform: rotateZ(360deg) scale(20);
  }
}

.happiness {
  width: 100%;
  padding: 91px 0 57px;
  background-image: url("../images/kodawari-bg@2x.webp");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.happiness__title {
  margin-top: -55px;
}

.happiness__inner {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}

.happiness__items {
  display: flex;
  flex-direction: row-reverse;
  gap: 40px;
  margin-right: 30px;
}

.happiness__link {
  margin-right: 80px;
  margin-top: -300px;
}

.happiness__link-tab {
  display: none;
}

.button-tab a {
  width: 100%;
  font-size: 3.6rem;
  color: #591902;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.02em;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5.2rem;
  border-radius: 100vh;
  border: solid 2px #591902;
  position: relative;
}

.button-tab a:hover span {
  right: 25%;
  transition: all 0.3s ease;
}

.button-tab a span {
  display: block;
  width: 1.6rem;
  height: 3.2rem;
  position: absolute;
  top: 50%;
  right: 27%;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}

.tategaki-button a {
  font-size: 20px;
  color: #ffffff;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.1em;
  padding: 50px 25px;
  background-color: transparent;
  border: solid 2px #ffffff;
  transition: all 0.3s ease;
}

.tategaki-button a:hover {
  background-color: #ffffff;
  color: #591902;
  transition: all 0.3s ease;
}

.section__title_size_s {
  font-size: 48px;
  color: #ffffff;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 0.1em;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
}

.cta__inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 260px;
}

.cta__belt {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.016em;
  display: block;
  text-align: center;
  background-color: #bf8c4e;
  padding: 2px 0;
}

.cta__tel {
  font-size: 32px;
  color: #bf8c4e;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.032em;
  margin-top: 4px;
}

.cta__tel span {
  font-size: 24px;
  color: #bf8c4e;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0.032em;
}

.cta-button {
  display: none;
}

.cta-button__inner {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cta-button__icon {
  width: 5.3rem;
  aspect-ratio: 53/69;
}

.cta-button__icon img {
  height: auto;
}

.cta-button__tel {
  font-size: 3.2rem;
  color: #ffffff;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 0;
  margin-top: 2rem;
}

.tategaki-comp__title {
  font-size: 32px;
  color: #ffffff;
  font-weight: 600;
  line-height: 1;
  letter-spacing: 0.1em;
  position: relative;
  white-space: nowrap;
  font-feature-settings: "palt";
  font-feature-settings: "pkna";
}

.tategaki-comp__title span {
  display: inline-block;
  width: 84px;
  aspect-ratio: 1/1;
  position: absolute;
  bottom: 101%;
  left: 50%;
  transform: translateX(-50%);
}

.tategaki-comp__title span img {
  height: auto;
}

.tategaki-comp__text-box {
  font-size: 16px;
  color: #ffffff;
  font-weight: 600;
  line-height: 2;
  letter-spacing: 0.1em;
  max-height: 520px;
  margin-right: 24px;
}

.container {
  width: 1232px;
  margin: 0 auto;
}

.wide-container {
  width: 1760px;
  margin: 0 auto;
}

@media only screen and (max-width: 1760px) {
  .wide-container {
    width: 94%;
  }
}

@media only screen and (max-width: 1232px) {
  html {
    font-size: 0.8116883117vw;
  }

  .happiness {
    padding: 9.1rem 5.7rem;
  }

  .happiness__title {
    margin-top: -5.5rem;
  }

  .happiness__items {
    gap: 4rem;
    margin-right: 3rem;
  }

  .happiness__link {
    margin-right: 8rem;
    margin-top: -30rem;
  }

  .tategaki-button a {
    font-size: 2rem;
    padding: 5rem 2.5rem;
  }

  .section__title_size_s {
    font-size: 4.8rem;
  }

  .tategaki-comp__title {
    font-size: 3.2rem;
  }

  .tategaki-comp__title span {
    width: 8.4rem;
  }

  .tategaki-comp__text-box {
    font-size: 1.6rem;
    max-height: 52rem;
    margin-right: 2.4rem;
  }

  .container {
    width: 94%;
  }
}

@media only screen and (max-width: 856px) {
  html {
    font-size: 1.1682242991vw;
  }

  .happiness {
    background-image: url("../images/merit-bg@2x.webp");
    padding: 3.4rem 0 6.2rem;
  }

  .happiness__inner {
    flex-direction: column;
  }

  .happiness__items {
    flex-direction: column;
    align-items: center;
    gap: 20rem;
    margin-right: 0;
    margin-top: 17.4rem;
  }

  .happiness__link {
    display: none;
  }

  .happiness__link-tab {
    display: block;
    margin-top: 12rem;
  }

  .section__title_size_s {
    font-size: 8rem;
  }

  .cta {
    display: none;
  }

  .cta-button {
    display: block;
    width: 16rem;
    height: 16rem;
    background-color: #bf8c4e;
    position: absolute;
    top: 0;
    right: 0;
  }

  .tategaki-comp__title {
    font-size: 7.2rem;
  }

  .tategaki-comp__title span {
    width: 19rem;
  }

  .tategaki-comp__text-box {
    font-size: 3.6rem;
    max-height: 117.7rem;
    margin-right: 5.4rem;
  }

  .container {
    width: 91.1%;
  }

  .wide-container {
    width: 100%;
  }
}

@media only screen and (max-width: 560px) {
  a[href^="tel:"] {
    pointer-events: auto;
  }
}